<template>
    <div>
        <van-cell-group>
        <van-field
        v-model="phone"
        label="手机号"
        placeholder="请输入手机号"
        @touchstart.stop="show = true"
        >
        </van-field>
        <van-field
            v-model="username"
            label="用户名"
            placeholder="请输入用户名"
            @touchstart.stop="show = true"
        />
        <van-field
            v-model="password"
            type="password"
            label="密码"
            placeholder="请输入密码"
            @touchstart.stop="show = true"
        />
        <van-field
        v-model="sms"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
        @touchstart.stop="show = true"
        >
        <van-button slot="button" size="small" type="primary" @click="yzm()">发送验证码</van-button>
        </van-field>
        </van-cell-group>
            <van-number-keyboard
            :show="show"
            extra-key="."
            close-button-text="完成"
            @blur="show = false"
            @input="onInput"
            @delete="onDelete"
            />
        <van-button size="large" type="info" @click="login()">注册</van-button>
    </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import axios from 'axios'
import {Toast} from 'vant'
export default {
    name:"Zhuce",
    data() {
        return {
            titles:"注册",
            username:"",
            password:"",
            phone:"",
            sms:"",
            show:false,
        }
    },
    mounted() {
        this.$emit('click',this.titles)
       
    },
    methods: {
        yzm(){
            var reg = /^1\d{10}$/;
            if(reg.test(this.phone)){
                Toast('验证码已发送');
                axios({
                    url:"/api/user/codemsg",
                    method:"get",
                    params:{phone:this.phone}
                }).then((data)=>{
                    console.log(data)
                })
            }
        },
        login(){
             var _this=this;
             axios({
                method:'post',
                url:"/api/user/register",
                params:{
                    code:this.sms,
                    name:this.username,
                    password:this.password,
                    phone:this.phone
                },
            }).then((data)=>{
                console.log(data.data)
                if(data.data.msg=="注册成功"){
                    _this.$router.push('/denglu')
                }
                if(data.data.msg=="验证码错误"){
                    Toast('验证码错误')
                }
            })
        },
        onInput(key){
            this.phone = (this.phone+key).slice(0,10)
        },
        onDelete(){
            this.phone = this.phone.slice(0,this.phone.length-1)
        }
    }
}
</script>
<style scoped>
    .van-button{
        margin-top: 30px;
    }
</style>



