<template>
    <div>
        <van-nav-bar
            title="我的"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
        />
        <van-cell icon="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1465374398,3300390172&fm=26&gp=0.jpg" class="first" v-tap='{methods:clicks}'>
            <van-col span='12'>{{username}}</van-col>
        </van-cell>
            <van-cell title="设置" to="/Shezhi"/>
            <van-cell title="宠物订单" to="/Dingdan"/>
            <van-cell title="我的地址" to="/Dizhi"/>
            <van-cell title="身份认证"/>
            <van-cell title="商家入驻"/>
            <van-cell title="关于"/>
    </div>
</template>
<script>
import {mapGetters} from 'vuex'
export default {
    methods: {
        onClickLeft(){
            
        },
        clicks(){
            this.$router.push('/Denglu')
        }
    },
    computed: {
        ...mapGetters(['username'])
    },
}
</script>
<style scoped>
    .first{
        background: #F3F1F1;
        
    }
    .van-cell__value{
        height: 90px;
        line-height: 90px;
        color: #101010
        
    }
    span{
        color: #101010;
        font-size: 12px;
    }
    .van-icon--image{
        width: 60px;
        height: 60px;
        margin: 15px 15px 0 0;

    }
    .list{
        margin-top: 10px;
    }
    .van-cell-group{
        border-bottom: 1px solid #d5d5d5
    }
</style>


