<template>
    <div>
        <ul class="first">
            <li v-for="item in list">{{item.name}}</li>
        </ul>
        <ul class="list">
            <li v-for="item in list">
                <img :src="item.img" alt="">
                <p>{{item.name}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Fenlei',
    data() {
        return {
            list:''
        }
    },
    mounted () {
        var _this=this
        axios({
            url:'http://www.jd.com/api/user',
        }).then((data)=>{
            _this.list=data.data.user
        })
    }
}
</script>
<style scoped>
    *{
        padding: 0;
        margin: 0
    }
    .first{
        float: left;
        padding-top: 10px;
        border-right: 1px solid #d5d5d5
    }
    .first li{
        width: 70px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        
    }
    .list{
        text-align: center
    }
    .list li{
        float: left;
        margin: 0 17px;
        padding-top: 10px
    }
</style>



