<template>
  <div>
    <van-nav-bar :title="name" left-text="返回" left-arrow @click-left="onClickLeft" fixed>
      <van-icon name="search" slot="right" />
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  name: 'Head',
  props:['name'],
  data () {
    return {
      title:''
    }
  },
  methods: {
    onClickLeft(){
      this.$router.go(-1)
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



