<template>
    <div>
        <van-row type="flex" justify="space-around" class="last">
            <van-col span="11" v-for="item in list" v-tap='{methods:detail,id:item.id}'>
                <img :src="item.picurl" alt="">
                <div>{{item.name}}</div>
                <div>{{item.date}}</div>
                <div>{{item.region}}</div>
            </van-col>
        </van-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Buys',
    data() {
        return {
            list:''
        }
    },
    methods: {
        onClickLeft(){
            this.$router.go(-1)
        },
        detail(id){
            this.$router.push({name:'Detail',query:{id:id.id}})
        }
    },
    mounted () {
        var _this=this
        axios({
            url:"http://47.104.244.134:8080/goodsbytid.do",
            params:{tid:13,page:1,limit:13}
        }).then((data)=>{
            //console.log(data.data.data)
            _this.list=data.data.data
        })
    }
}
</script>
<style scoped>
    .last{  
        text-align: center;
        flex-wrap: wrap;
    }
    img{
        width: 145px;
        height: 169px;
    }
</style>




