<template>
    <div>
        <van-row>
            <van-col span='6' v-for="(item,i) in list" :key=i>{{item}}</van-col>
        </van-row>
    </div>
</template>
<script>
export default {
    name:"Dingdan",
    data() {
        return {
            title:'宠物订单',
            list:['待支付','已支付','已完成','已取消']
        }
    },
    mounted() {
        this.$emit('click',this.title)
    },
}
</script>
<style scoped>
    .van-col--6{
        text-align: center;
        height: 48px;
        line-height: 48px
    }
</style>



