<template>
    <div>
        <van-nav-bar
        title="宠物详情"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        />
        
        <van-swipe class="goods-swipe" :autoplay="3000">
        <van-swipe-item class="goods-swipe">
            <img :src="item.picurl" >
        </van-swipe-item>
        </van-swipe>
        <van-cell-group>
            <van-cell>
                <div class="goods-title"> {{item.info}}
                    <span class="span">{{item.price+'元'}}</span>
                </div>
                <div class="goods-name">{{item.info}}</div>
                <div>
                    <van-tag round>DD</van-tag>
                    <van-tag round>黄色</van-tag>
                    <van-tag round>1个月22天</van-tag>
                </div>
            </van-cell>
            <van-cell>
                <div class="city">{{'城市：'+item.info}}</div>
                <div>
                    <van-col span="8"><span>配送：</span>自提0元</van-col>
                    <van-col span="8" style="text-align: center">同城0元</van-col>
                    <van-col span="8">外地500元</van-col>
                </div>
            </van-cell>
        </van-cell-group>
        <van-cell-group>
            <van-cell>
                <div >
                    担保交易
                </div>
                <div class="city">
                    此交易平台为您的交易全程提供保障
                    <van-icon name="arrow" />                                         
                </div>
                <div class="city">
                    <van-col span='6'>监控商家</van-col>
                    <van-col span='6'>一宠一拍</van-col>
                    <van-col span='6'>30天保障</van-col>
                    <van-col span='6'>先行赔付</van-col>
                </div>
            </van-cell>
        </van-cell-group>
        <van-cell-group>
            <van-cell>
                <div class="city">安家大礼包</div>
                <div class="city citys">
                    通过此平台购买宠物可获得一份安家大礼包哦~
                    <van-icon name="arrow" />  
                </div>
            </van-cell>
        </van-cell-group>
        <van-cell-group>
            <van-cell title="壹五零九狗舍" icon="shop-o" value="进入店铺" is-link></van-cell>
        </van-cell-group>
        <van-goods-action>
            <van-goods-action-mini-btn
                icon="chat-o"
                text="客服"
            />
            <van-goods-action-mini-btn
                icon="cart-o"
                text="购物车"
            />
            <van-goods-action-mini-btn
                icon="shop-o"
                text="店铺"
            />
            <van-goods-action-big-btn text="加入购物车" />
            <van-goods-action-big-btn
                primary
                text="立即购买"
                @click="show=true"
            />
            <van-popup v-model="show" position="bottom" overlay>
                <van-row>
                    <van-col span='24'>确认支付</van-col>
                    <van-col span='14'>{{item.price+'元'}}</van-col>
                </van-row>
                <van-cell-group>
                    <van-cell title="订单信息" value="带我护家-支付" />
                    <van-cell title="付款方式" value="中国工商银行"  is-link/>
                </van-cell-group>
                <van-tabbar v-model="active">
                        <van-col span='12'>
                            <van-button type="default" @click="show=false">取消</van-button>   
                        </van-col>
                        <van-col span='12' class="right">
                        <van-button type="danger" @click="que()">确认</van-button>   
                        </van-col>
                </van-tabbar>
            </van-popup>
        </van-goods-action>
    </div>
</template>
<script>
import axios from 'axios';
import { Dialog } from 'vant';
export default {
    data() {
        return {
            item:'',
            show:false,
            active:0
        }
    },
    mounted() {
        var _this=this
        axios({
            url:"http://47.104.244.134:8080/goodsbyid.do",
            params:{id:this.$route.query.id}    
        }).then((data)=>{
            //console.log(data.data)
            _this.item=data.data
        })
    },
    methods: {
        onClickLeft(){
            this.$router.go(-1)
        },
        onClickBigBtn(){
            this.show =! this.show
        },
        que(){
            Dialog.alert({
            message: '支付完成'
            }).then(() => {
                this.$router.push('/main')
            });
        }
    },
}
</script>
<style scoped>
    .goods-swipe{
        width: 100%;
        display: block;
        text-align: center
    }
    .goods-title{
        font-size: 16px;
    }
    .span{
        float: right;
        color: #f44
    }
    .goods-name{
        color: #d5d5d5
    }
    .van-col--8{
        color: #101010;
        font-size: 16px;
    }
    .van-col--8 span{
        color: #d5d5d5
    }
    .city{
        padding: 2px 0
    }
    .van-col--6{
        color: #d5d5d5
    }
    .city .van-icon{
        margin-left: 100px;
    }
    .citys .van-icon{
        margin-left: 33px;
    }
    .van-col--24{
        text-align: center;
        border-bottom: 1px solid #d5d5d5;
        height: 50px;
        line-height: 50px;
    }
    .van-popup{
        height: 432px;
    }
    .van-col--14{
        text-align: right;
        height: 100px;
        line-height: 100px;
        font-size: 24px;
    }
    .van-col--12{
        text-align: center;
        margin: 0 10px;
    }
</style>



