<template>
    <div>
        <van-cell-group>
            <van-cell>
                <van-col span='4'>
                    <van-tag plain size='large' @click='top()'>返回</van-tag>
                </van-col>
                <van-col span="18">
                    <span>萌宠</span>
                    <span>宠舍</span>
                </van-col>
            </van-cell>
        </van-cell-group>
        <van-row class="first">
            <van-col span="12" v-tap='{methods:tap}'>
                分类
                <van-icon name="arrow-down"/>
            </van-col>
            <van-col span="12" v-tap='{methods:tpp}'>
                筛选
                <van-icon name="arrow-down"/>
            </van-col>
        </van-row>
        <router-view></router-view>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Buy',
    data() {
        return {
            
        }
    },
    methods: {
        tap(){
            this.$router.push('/mark')
        },
        top(){
            this.$router.go(-1)
        },
        tpp(){
            this.$router.push('/screen')
        }
    }
}
</script>
<style scoped>
    .first{
        text-align: center;
        height: 46px;
        line-height: 46px;
    }
    .van-col--18 span{
        margin: 0 37px;
        font-size: 18px;
    }
</style>


