<template>
    <div>
        <Alike :name="title"/>
        <router-view @click="tap"></router-view>
    </div>
</template>
<script>
import Alike from '@/components/Alike'
export default {
    name:'Alikes',
    data() {
        return {
            title:''
        }
    },
    components:{
        Alike
    },
    methods: {
        tap(msg){
            this.title=msg
        }
    }
}
</script>
<style scoped>

</style>



