<template>
    <div>
        <van-row class="top">
            <van-col span="24">推荐品牌</van-col>
        </van-row>
        <van-row type="flex" justify="space-around" class="tap">
            <van-col span="7" v-for="(item,i) in list" :key="i">
                <img :src="item.brandImg" alt="">
                <p>{{item.brandName}}</p>
                <p class="first">{{item.brandArea}}</p>
            </van-col>  
        </van-row>
        <van-row class="top">
            <van-col span="24">主粮品牌</van-col>
        </van-row>
        <van-row type="flex" justify="space-around" class="tap">
            <van-col span="7" v-for="(item,i) in snackslist" :key="i">
                <img :src="item.brandImg" alt="">
                <p>{{item.brandName}}</p>
                <p class="first">{{item.brandArea}}</p>
            </van-col>  
        </van-row>
        <van-row class="top">
            <van-col span="24">零食品牌</van-col>
        </van-row>
        <van-row type="flex" justify="space-around" class="tap">
            <van-col span="7" v-for="(item,i) in staplelist" :key="i">
                <img :src="item.brandImg" alt="">
                <p>{{item.brandName}}</p>
                <p class="first">{{item.brandArea}}</p>
            </van-col> 
        </van-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Pin',
    data () {
        return {
            list:'',
            snackslist:"",
            staplelist:''
        }
    },
    mounted () { 
        //推荐品牌
        axios({
            url:"http://39.96.73.101:8080/pet/brand/list.do"
        }).then((data)=>{
            this.list = data.data.data
            //console.log(data.data.data)
        })
        //主粮品牌
        axios({
            url:"http://39.96.73.101:8080/pet/brand/staplelist.do"
        }).then((data)=>{
            this.snackslist = data.data.data
            //console.log(this.snackslist)
        })
        //零食品牌
        axios({
            url:'http://39.96.73.101:8080/pet/brand/snackslist.do'
        }).then((data)=>{
            this.staplelist = data.data.data
        })
    }
}
</script>
<style scoped>
    .top{
        height: 30px;
        line-height: 30px;
        text-align: center
    }
    .tap{
        text-align: center;
        flex-wrap: wrap;
        border-top: 5px solid #d5d5d5
    }
    img{
        width: 100%
    }
    p{
        padding: 0;
        margin: 0;
        font-size: 14px;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        margin: 2px 0
    }
    .van-col--7{
        margin-top: 10px;
    }
    .first{
        color: #d5d5d5
    }
    
</style>



