import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Community from '@/views/Community'
import Buy from '@/views/Buy'
import Advices from '@/views/Advices'
import My from '@/views/My'
import Main from '@/views/Main'
import Detail from '@/views/Detail'
import Shop from '@/views/Shop'
import Store from '@/views/Store'
import Cat from '@/views/Cat'
import Classify from '@/views/Classify'
import Fenlei from '@/views/Fenlei'
import Pin from '@/views/Pin'
import Buys from '@/views/Buys'
import Mark from '@/views/Mark'
import Screen from '@/views/Screen'
import Alikes from '@/views/Alikes'
import Shezhi from '@/views/Shezhi'
import Dingdan from '@/views/Dingdan'
import Dizhi from '@/views/Dizhi'
import Denglu from '@/views/Denglu'
import Zhuce from '@/views/Zhuce'
import QQ from '@/views/QQ'
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            redirect: '/main',
            name: 'Index',
            component: Index,
            children: [{
                    path: '/community',
                    name: 'Community',
                    component: Community
                }, {
                    path: "/buy",
                    name: 'Buy',
                    redirect: '/buys',
                    component: Buy,
                    children: [{
                        path: '/buys',
                        name: Buys,
                        component: Buys
                    }]
                },
                {
                    path: "/advices",
                    name: 'Advices',
                    component: Advices
                }, {
                    path: '/my',
                    name: 'My',
                    component: My
                }, {
                    path: '/main',
                    name: "Main",
                    component: Main
                }
            ]
        },
        {
            path: '/detail',
            name: 'Detail',
            component: Detail
        },
        {
            path: '/shop',
            name: 'Shop',
            redirect: '/store',
            component: Shop,
            children: [{
                path: '/store',
                name: 'Store',
                component: Store
            }, {
                path: '/cat',
                name: 'Cat',
                component: Cat,
            }, {
                path: '/classify',
                name: 'Classify',
                redirect: '/Fenlei',
                component: Classify,
                children: [{
                    path: '/fenlei',
                    name: '/Fenlei',
                    component: Fenlei
                }, {
                    path: '/pin',
                    name: '/Pin',
                    component: Pin
                }]
            }]
        }, {
            path: '/mark',
            name: 'Mark',
            component: Mark
        }, {
            path: '/screen',
            name: "Screen",
            component: Screen
        }, {
            path: '/alikes',
            name: "Alikes",
            component: Alikes,
            children: [{
                path: '/shezhi',
                name: "Shezhi",
                component: Shezhi
            }, {
                path: "/dingdan",
                name: 'Dingdan',
                component: Dingdan
            }, {
                path: "/dizhi",
                name: 'Dizhi',
                component: Dizhi
            }, {
                path: '/denglu',
                name: 'Denglu',
                component: Denglu
            }, {
                path: "/zhuce",
                name: "Zhuce",
                component: Zhuce
            }]
        }, {
            path: "/qq",
            name: "QQ",
            component: QQ
        }

    ]
})


