<template>
    <div>
        <div class="container">
             <router-view></router-view>
        </div>
        <van-tabbar v-model="active">
            <van-tabbar-item icon="home-o" to='/main'>首页</van-tabbar-item>
            <van-tabbar-item icon="wap-home" to="/community">社区</van-tabbar-item>
            <van-tabbar-item icon="cart-o" to="/buy">购宠</van-tabbar-item>
            <van-tabbar-item icon="comment-o" to="/advices">消息</van-tabbar-item>
            <van-tabbar-item icon="contact" to="/my">我的</van-tabbar-item>  
        </van-tabbar>
    </div>
</template>
<script>
export default {
    name:"Index",
    data() {
        return {
            active:0
        }
    },
}
</script>
<style scoped>
    .container{
        margin-bottom: 50px
    }
</style>



