<template>
    <div>
        <van-cell-group>
  <van-field
    v-model="username"
    clearable
    label="绑定手机号"
    placeholder="请输入手机号"
  />
    <van-cell title="清除缓存"/>
    <van-button size="large" type="primary">退出登录</van-button>
</van-cell-group>        
    </div>
</template>
<script>
export default {
    name:"Shezhi",
    data() {
        return {
            title:'设置',
            username:'',
        }
    },
    mounted() {
        this.$emit('click',this.title)
    },
}
</script>
<style scoped>

</style>



