<template>
    <div>
        <van-row>
            <van-col span="12" v-tap='{methods:tap}'>
                分类
                <van-icon name="arrow-down"/>
            </van-col>
            <van-col span="12" v-tap='{methods:tap1}'>
                品牌
                <van-icon name="arrow-down"/>
            </van-col>
        </van-row>
    </div>
</template>
<script>
export default {
    name:'Cat',
    data() {
        return {
            title:'猫猫',
            activeKey: 0
        }
    },
    mounted() {
        this.$emit('toTitle',this.title)
    },
    methods: {
        tap(){
            this.$router.push('/Fenlei')
            
        },
        tap1(){
            this.$router.push('/Pin')
        }
    }
}
</script>
<style scoped>
    .van-row{
        text-align: center;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid #D5D5D5
    }
</style>



