<template>
    <div>
        <van-cell-group>
            <van-cell>
                <van-col span='4'>
                    <van-tag plain size='large' @click='top()'>返回</van-tag>
                </van-col>
                <van-col span="18">
                    <span>萌宠</span>
                    <span>宠舍</span>
                </van-col>
            </van-cell>
        </van-cell-group>
        <van-row class="first">
            <van-col span="12" v-tap='{methods:tap}'>
                分类
                <van-icon name="arrow-down"/>
            </van-col>
            <van-col span="12" v-tap='{methods:tep}'>
                筛选
                <van-icon name="arrow-down"/>
            </van-col>
        </van-row>
        <ul class="ul-first">
            <li v-for="item in list">{{item.name}}</li>
        </ul>
        <ul class="ul-list">
            <li v-for="item in list">
                <img :src="item.img" alt="">
                <p>{{item.name}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:"Mark",
    data() {
        return {
            list:''
        }
    },
    mounted () {
        var _this=this
        axios({
            url:'http://www.jd.com/api/user',
        }).then((data)=>{
            _this.list=data.data.user
        })
        axios({
            url:"api/type/catTypelist.do",
            params:{type:1}
        }).then((data)=>{
            console.log(data.data)
        })
    },
    methods: {
        tap(){
            this.$router.push('/mark')
        },
        top(){
            this.$router.go(-1)
        },
        tep(){
            this.$router.push('/screen')
        }
    },
}
</script>
<style scoped>
    p{
        padding: 0;
        margin: 0
    }
    .ul-first{
        float: left;
        padding-top: 10px;
        border-right: 1px solid #d5d5d5
    }
    .ul-first li{
        width: 70px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        
    }
    .ul-list{
        text-align: center
    }
    .ul-list li{
        float: left;
        margin: 0 17px;
        padding-top: 10px
    }
     .first{
        text-align: center;
        height: 46px;
        line-height: 46px;
        border-bottom: 1px solid #d5d5d5
    }
    .van-col--18 span{
        margin: 0 37px;
        font-size: 18px;
    }
</style>



