<template>
    <div>
        <van-nav-bar
            title="荣耀水晶"
            left-text="返回"
            left-arrow
            />
        <van-notice-bar
        text="少年，我看你骨骼精奇，是万中无一的电竞奇才，维护王者世界和平就靠你了，我这有个链接--->《免费领取荣耀水晶》，见与你有缘，变送给你了！"
        left-icon="volume-o"
        /> 

        <van-button @touchstart.stop="show = true">
            <van-cell-group>
                <van-field v-model="value" placeholder="请输入QQ号" />
            </van-cell-group>
        </van-button>
        <van-row>
            <van-col span="24"></van-col>
        </van-row>

        <van-number-keyboard
        :show="show"
        extra-key="."
        close-button-text="关闭"
        @blur="show = false"
        @input="onInput"
        @delete="onDelete"
        />
        
        <van-tabbar v-model="active">
            <van-button type="danger" size="large">点击免费荣耀水晶</van-button>
        </van-tabbar>
    </div>
</template>onDelete
<script>
export default {
    name:"QQ",
    data() {
        return {
            active:0,
            show:false,
            value:''
        }
    },
    methods: {
        onInput(key){
            this.value = (this.value+key).slice(0,10)
        },
        onDelete(){
            this.value = this.value.slice(0,this.value.length-1)
        }
    },
}
</script>
<style scoped>
    .van-button--normal{
        width: 100%;
    }
    .van-col--24{
        height: 500px;
    }
</style>



