<template>
    <div>
        <van-cell-group>
        <van-field
            v-model="phone"
            label="手机号"
            placeholder="请输入手机号"
        />
        <van-field
            v-model="password"
            type="password"
            label="密码"
            placeholder="请输入密码"
        />
        </van-cell-group>
        <van-button size="large" type="danger" @click="login({username:phone,password:password})">登录</van-button>
        <van-row>
            <van-col span='12' v-tap='{methods:zhuce}'>快速注册</van-col>
            <van-col span='12'>忘记密码</van-col>
        </van-row>
        <van-popup v-model="show" >
            <van-col span="12" v-tap='{methods:QQ}'>
                <img src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2487116545,1388145081&fm=26&gp=0.jpg" alt="">
                <div>QQ登录</div>    
            </van-col>
            <van-col span="12">
                <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2364754101,3807084153&fm=26&gp=0.jpg" alt="">
                <div>微信登录</div>
            </van-col>
        </van-popup>
        <van-tabbar v-model="active">
            <van-button size="large" type="info" @click="show=true">其他方式登录</van-button>
        </van-tabbar>
    </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex';
import axios from "axios"
export default {
    name:"Denglu",
    data() {
        return {
            titles:"登录",
            phone:"",
            password:"",   
            active:0,
            show:false
        }
    },
    methods: {
        ...mapActions(['login']),
        QQ(){
            this.$router.push('/QQ')
        },
        zhuce(){
            this.$router.push('/Zhuce')
        },
    },
    mounted() {
        this.$emit('click',this.titles)
    }
}
</script>
<style scoped>
    .van-cell-group{
        margin-bottom: 10px;
    }
    .van-row{
        margin-top: 20px;
    }
    .van-col--12{
        margin-top: 35px;
        text-align: center
    }
    .van-col--12 img{
        width: 85px;
        height: 75px;
    }
    .van-popup{
        width: 80%;
        height: 20%;
    }
</style>



