            <van-field v-model="value" placeholder="地址" />
            <van-field v-model="value" placeholder="详细地址" />
        </van-cell-group>
    </div>
</template>
<script>
export default {
    name:"Dizhi",
    data() {
        return {
            title:'地址信息',
            value:''
        }
    },
    mounted() {
        this.$emit('click',this.title)
    },
}
</script>
