import Mock from 'mockjs'
var user = Mock.mock({
    'user|16': [{
        "name": "@cname",
        'img': '@image(size(145x169))',
        'id|+1': 0,
        'date': '@date',
        'region': "@region"
    }]
})
Mock.mock('http://www.jdd.com/api/user', user)


