<template>
    <div>
        <Head :name="title"/>
        <div class="container">
            <router-view @toTitle="tap"></router-view>
        </div>
        <van-tabbar/>
    </div>
</template>
<script>
import Head from '@/components/Head'
export default {
    name:'Shop',
    data() {
        return {
            title:''
        }
    },
    components:{
        Head
    },
    methods: {
        tap(msg){
            this.title=msg
        }
    },
}
</script>
<style scoped>
    .van-tabbar{
        height: auto;
    }
    .van-tabbar{
        height: auto;
    }
    .container{
        margin-top: 46px
    }
</style>



