<template>
    <div>
        <van-tabs v-model="active" :swipe-threshold=5 :sticky=true offset-top:0px>
            <van-tab title="圈子">
                <van-row>
                    <van-col span='12'>我的圈子</van-col>
                    <van-col span='12'>更多圈子</van-col>
                </van-row>
                <van-card
                v-for="item in list"
                :desc="item.text"  
                :title="item.name"
                :thumb="item.img"
                :centered=true
                />
            </van-tab>
            <van-tab title="关注">内容 2</van-tab>
            <van-tab title="最新">内容 3</van-tab>
            <van-tab title="热门">内容 4</van-tab>
            <van-tab title="推荐">内容 5</van-tab>
        </van-tabs>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            active:0,
            list:[]
        }
    },
    mounted () {
        var _this=this
        axios({
            url:'http://www.taobao.com/api/user'
        }).then((data)=>{
            console.log(data.data)
            _this.list=data.data.user
        })
    }
}
</script>
<style scoped>
   .van-col--12{
       text-align: center;
       height: 46px;
       line-height: 46px
   }
    .van-card__thumb img{
        margin-top: 17px;
    }
</style>



