import axios from 'axios'
import router from '@/router'
import { Toast } from 'vant'
var actions = {
    login({ commit }, { username: username, password: password }) {
       
            axios({
                method: 'post',
                url: 'http://39.96.73.101:8080/pet/user/login.do',
                params: {
                    password: password,
                    phone: username
                }
            }).then((data) => {
                console.log(data)
                commit('login', username)
                if (data.data.msg == '登陆成功') {
                    router.push('/my')
                }
                if (data.data.msg == "用户名或密码不正确") {
                    Toast('用户名或者密码错误')
                }
            })
    }
}
export default actions;


