<template>
    <div>
        <van-cell-group>
            <van-cell>
                <van-col span='4'>
                    <van-tag plain size='large' @click='top()'>返回</van-tag>
                </van-col>
                <van-col span="18">
                    <span>萌宠</span>
                    丨
                    <span>宠舍</span>
                </van-col>
            </van-cell>
        </van-cell-group>
        <van-row class="first">
            <van-col span="12" v-tap='{methods:tap}'>
                分类
                <van-icon name="arrow-down"/>
            </van-col>
            <van-col span="12" v-tap='{methods:tep}'>
                筛选
                <van-icon name="arrow-down"/>
            </van-col>
        </van-row>
        <van-cell-group>
            <van-cell title="排序">
            </van-cell>
            <van-cell>
                <span class="collection-item"
                    v-for="gameName in gameNames"
                    @click="selected(gameName)"
                    :class="{activ: activeName == gameName}">{{gameName}}
                </span>
            </van-cell>  
        </van-cell-group>
        <van-cell-group>
            <van-cell title="价格">
            </van-cell>
            <van-cell>
                <span class="collection-item"
                    v-for="gameName1 in gameNames1"
                    @click="selected1(gameName1)"
                    :class="{activ: activeName1 == gameName1}">{{gameName1}}
                </span>
            </van-cell>  
        </van-cell-group>
        <van-cell-group>
            <van-cell title="性别">
            </van-cell>
            <van-cell>
                <span class="collection-item"
                    v-for="gameName2 in gameNames2"
                    @click="selected2(gameName2)"
                    :class="{activ: activeName2 == gameName2}">{{gameName2}}
                </span>
            </van-cell>  
        </van-cell-group>
        <van-button type="default" v-tap="{methods:reset}">重置</van-button>
        <van-button type="danger" class="btn-class" to='/buy' :disabled='disabled'>确认</van-button>
    </div>
</template>
<script>
export default {
    name:"Screen",
    data() {
        return {
            disabled:true,
            active:0,
            gameNames: ['价格升序', '价格降序','最新','推荐'],
            activeName: '',
            gameNames1: ['2000元以上', '2000元以下','1000元以上',],
            activeName1: '',
            gameNames2: ['DD', 'MM',],
            activeName2: '',
            active:0
        }
    },
    mounted () {
        
    },
    methods: {
        tap(){
            this.$router.push('/mark')
        },
        top(){
            this.$router.go(-1)
        },
        tep(){
            this.$router.push('/screen')
        },
        selected(gameName) {
            this.activeName = gameName;
            this.disabled=false;
        },
        selected1(gameName1) {
            this.activeName1 = gameName1
            this.disabled=false;
        },
        selected2(gameName2) {
            this.activeName2 = gameName2
            this.disabled=false;
        },
        reset:function () {
            this.activeName=this.activeName1=this.activeName2=''
            this.disabled=true;
        }
    },
}
</script>
<style scoped>
     .first{
        text-align: center;
        height: 46px;
        line-height: 46px;
        border-bottom: 1px solid #d5d5d5
    }
    .van-col--18 span{
        margin: 0 37px;
        font-size: 18px;
    }
    .van-col--8{
        border: 1px solid #d5d5d5;
        margin: 0 12px;
        text-align: center
    }
    .van-col--12{
        text-align: center;
        line-height: 50px
    }
    .activ{
        background: #f44
    }
    .collection-item{
        display: inline-table;
        margin: 5px 20px;
        border: 1px solid #d5d5d5;
        width: 131px;
        height: 26px;
        text-align: center;
    }
    .van-button{
        position: absolute;
        bottom: 0;
        width: 49vw
    }
    .btn-class{
        margin-left:49vw
    }
</style>



