<template>
    <div class="container">
        <van-nav-bar title="宠" fixed>
            <van-icon name="search" slot="right" />
        </van-nav-bar>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :animation-duration='2000'>
            <van-swipe :autoplay="3000" >
                <van-swipe-item v-for="(image, index) in images" :key="index">
                <!-- 遍历请求到的数据 -->
                    <img :src="image.banner_img_url" :height='144' :width="375"/>
                </van-swipe-item>
            </van-swipe>
            <van-row class="user-links">
                <van-col span="6">
                    <van-icon name="location" />
                    附近
                </van-col>
                <van-col span="6" v-tap='{methods:shop}'>
                    <van-icon name="bag" />
                    商城
                </van-col>
                <van-col span="6">
                    <van-icon name="hot-sale" />
                    宝宝热评
                </van-col>
                <van-col span="6">
                    <van-icon name="smile-comment" />
                    求助问答
                </van-col>
            </van-row>
            <!--  -->
            <van-card class="talon"
                
                :centered="true"	
                desc="萌宠逗趣欢乐日常" 
                title="爪事件"
                thumb="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1556555101207&di=1386ba7cefc81453c950e8f5ef78a2fd&imgtype=0&src=http%3A%2F%2Fimg8.soufunimg.com%2Falbum%2F2015_10%2F19%2FM04%2F00%2FB3%2FwKgEQFYkS5-IR9paAABdkMiRhmUAAIG6wK9ki4AAF2o134.gif"
                @click="comm()"
            >
            </van-card>
            <!-- 列表 -->
                <van-card
                @click="detail(item.id)"
                v-for="(item,id) in list"
                :key=id
                :price='item.price'
                :desc="item.name" 
                :title="item.info"
                :thumb="item.picurl"
            />
        </van-pull-refresh>
        <van-notice-bar wrapable :scrollable="false" background="#fff" color="#000" class="footer" v-tap='{methods:tap}'>
            查看更多萌宠<van-icon name="arrow" />
        </van-notice-bar>
    </div>
</template>
<script>
import axios from 'axios'
import {mapState} from 'vuex'
export default {
    data() {
        return {
            images:[],
            list:[],
            isLoading: false
        }
    },
    mounted() {
        var _this=this;
        //请求轮播图的数据
        axios({
            url:" http://jx.xuzhixiang.top/ap/api/bannerlist.php",
            params:{uid:1017}
        }).then((data)=>{
            _this.images = data.data.data
            //console.log(data.data.data)
        })
        //数据列表
        axios({
            url:"http://47.104.244.134:8080/goodsbytid.do",
            params:{tid:13,page:1,limit:13}
        }).then((data)=>{
            _this.list=data.data.data
        })
        axios({
            url:'api/type/catTypelist.do',
            params:{type:1}
        }).then((data)=>{
            console.log(data.data)
        })
    },
    methods: {
        comm(){
            this.$router.push('/Community')
        },
        detail(id){ 
            this.$router.push({name:'Detail',query:{id:id}})
        },
         onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
            }, 2000);
        },
        tap(){
            this.$router.push('/Buy')
        },
        shop(){
            this.$router.push('/Shop')
        }
    },
}
</script>
<style scoped>
    .user-links{
        font-size: 12px;
        text-align: center;
        background-color: #fff;
        margin-top: 10px;
        
    }
    .user-links .van-icon {
      display: block;
      font-size: 24px;
      line-height: 2
    }
    .talon{
        background: #fff;
        font-size: 14px;
        border-top: 10px solid #eeeeee;
        line-height: 90px;
    }
    .footer{
        font-size: 14px;
        text-align: center;
        height: 45px;
    }
</style>



