<template>
    <div>
        <van-swipe :autoplay="3000" >
            <van-swipe-item v-for="(image, index) in images" :key="index">
            <!-- 遍历请求到的数据 -->
                <img :src="image.banner_img_url" :height='144' :width="375"/>
            </van-swipe-item>
        </van-swipe>
        <van-row type="flex" justify="space-around" class="discount">
            <van-col span="7">
                <p> 5元<br>
                满60元可以使用
                </p>  
            </van-col>
            <van-col span="7">
                <p> 10元<br>
                满98元可以使用
                </p>  
            </van-col>
            <van-col span="7">
                 <p> 30元<br>
                满288元可以使用
                </p>  
            </van-col>
        </van-row>
        <van-row class="user-links">
            <van-col span="6"  v-tap='{methods:shop}'>
                <van-icon name="https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2111787690,1002547534&fm=5" size='4em'/>
                猫猫
            </van-col>
            <van-col span="6">
                <van-icon name="https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2300875363,445064071&fm=5" size='4em'/>
                狗狗
            </van-col>
            <van-col span="6">
                <van-icon name="hot" size='4em'/>
                拼团
            </van-col>
            <van-col span="6">
                <van-icon name="gift-card" size='4em'/>
                全球购
            </van-col>
        </van-row>
        <van-row>
            <van-col span='24'>特惠专区</van-col>
        </van-row>
        <van-row type='flex' justify="space-between">
            <van-col span='12' v-for="(item,i) in list" :key=i>
                <img :src="item.goodsImage" alt="">
                <p>{{item.goodsName}}</p>
                <p>3人团￥:{{item.goodsTypeId}}</p>
                <p>单买价￥：{{item.goodsPrice}}</p>
            </van-col>
        </van-row>
        <van-row>
            <van-col span='24'>新品上市</van-col>
        </van-row>
        <van-row type='flex' justify="space-between">
            <van-col span='12'>
                <img src="" alt="">
                <p>aaa</p>
                <p>bbb</p>
                <p>ccc</p>
            </van-col>
        </van-row>
        <van-row>
            <van-col span='24'>免税直邮</van-col>
        </van-row>
        <van-row type='flex' justify="space-between">
            <van-col span='12'>
                <img src="" alt="">
                <p>aaa</p>
                <p>bbb</p>
                <p>ccc</p>
            </van-col>
        </van-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'Store',
    data() {
        return {
            images:[],
            title:'商城',
            list:''
        }
    },
    mounted() {
        this.$emit('toTitle',this.title)
        var _this=this;
        //请求轮播图的数据
        axios({
            url:" http://jx.xuzhixiang.top/ap/api/bannerlist.php",
            params:{uid:1017}
        }).then((data)=>{
            _this.images = data.data.data
            //console.log(data.data.data)
        })
        axios({
            url:'api/goods/newgoodslist.do'
        }).then((data)=>{
            //console.log(data.data.data)
            _this.list = data.data.data
        })
    },
   methods: {
       shop(){
           this.$router.push('/Classify')
       }
   },
}
</script>
<style scoped>
    .discount{
        text-align: center;
        font-size: 14px;
        line-height: 30px;
        margin-top: 10px;
        height: 116px;
    }
    .discount .van-col{
        height: 101px;
        background: #eeeeee;
        margin: 0 5px;
    }
    .user-links{
        height: 81px;
        font-size: 12px;
        text-align: center;
        background-color: #fff;
        border-top: 10px solid #eeeeee;
        border-bottom: 10px solid #eeeeee;
        line-height: 3;
    }
    .user-links .van-icon {
      display: block;
      font-size: 24px;  
      width: auto;
    }
    .van-col--24{
        height: 39px;
        line-height: 39px;
        padding-left: 16px;
    }
    .van-row{
        flex-wrap: wrap
    }
    .van-col--12 img{
        width: 100%;
    }
    p{
        margin: 0;
        padding: 0;
        margin: 5px 0
    }
</style>



