<template>
    <div>
        <Cat/>
        <router-view></router-view>
    </div>
</template>
<script>
import Cat from '@/views/Cat'
export default {
    name:"Classify",
    components:{
        Cat
    },
    data() {
        return {
            title:'猫猫',
            activeKey: 0
        }
    },
    mounted() {
        this.$emit('toTitle',this.title)
    },
}
</script>
<style scoped>

</style>



